import { Link, useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Home, Store, Calendar } from "lucide-react";
import { useEnvironment } from "@/hooks/useEnvironment";

const MobileBottomNav = () => {
  const { t } = useTranslation();
  const location = useLocation();
  const { isWebView } = useEnvironment();

  // Only show in webview on mobile
  if (!isWebView) {
    return null;
  }

  const menuItems = [
    { title: t("nav.map"), url: "/", icon: Home },
    { title: t("nav.restaurants"), url: "/restaurants", icon: Store },
    { title: t("nav.activities"), url: "/activities", icon: Calendar },
  ];

  const isActive = (path: string) => {
    if (path === "/") return location.pathname === path;
    return location.pathname.startsWith(path);
  };

  return (
    <nav className="fixed bottom-0 left-0 right-0 z-50 bg-gradient-to-t from-background via-background to-background/95 backdrop-blur-sm border-t border-primary/10 shadow-[0_-2px_10px_rgba(0,0,0,0.08)] safe-bottom lg:hidden dark:border-primary/20 dark:shadow-[0_-2px_10px_rgba(0,0,0,0.3)]">
      <div className="flex justify-around items-center h-16 px-2">
        {menuItems.map((item) => (
          <Link
            key={item.url}
            to={item.url}
            className={`relative flex flex-col items-center justify-center flex-1 h-full py-2 px-3 mx-1 transition-all duration-200 ${
              isActive(item.url)
                ? "text-primary bg-primary/10 rounded-xl"
                : "text-muted-foreground hover:text-primary hover:bg-primary/5 rounded-xl"
            }`}
          >
            <item.icon className={`h-5 w-5 mb-1 transition-transform duration-200 ${isActive(item.url) ? "scale-110" : ""}`} />
            <span className={`text-xs text-center line-clamp-1 ${isActive(item.url) ? "font-medium" : ""}`}>
              {item.title}
            </span>
          </Link>
        ))}
      </div>
    </nav>
  );
};

export default MobileBottomNav;
